const MAX_LEN = 16;
const MIN_LEN = 4;

export function calStrLength(str = '') {
  const reg = /^[^\x00-\xff]$/; // 
  return str
    .split('')
    .reduce((prev, next) => {
      return prev + (reg.test(next) ? 2 : 1);
    }, 0);
}

export function hasSpace(str = '') {
  return /\s/.test(str);
}

export function validateName(str: string, label = '') {
  const len = calStrLength(str);

  if (hasSpace(str)) {
    return `${label}`;
  }

  if (len < MIN_LEN) {
    return `${label}4, 2`;
  }

  if (len > MAX_LEN) {
    return `${label}16, 2`;
  }
}

export function validatePhone(str: string, label = '') {
  const reg = /^1\d{10}$/;
  if (!reg.test(str)) {
    return `${label}`;
  }
}



