import * as decoder from 'js-base64';
import {
  STORY_MODE_SELECT,
  STORY_CARDS_COUNT_SELECT,
  STORY_QUESTION_TYPE_SELECT,
  STORY_QUESTION_INPUT,
  STORY_FORMATION_SELECT,
  STORY_PRIVATE,
  STORY_GET_RESULT,
  NEWBIE_QUESTION_GET_RESULT,
  GET_DAILY_DIVINE,
  GET_RESULT_BY_ID,
  CLEAN_RESULT
} from '../constants/actionType';
import { createReducer } from '../utils/createReducer';

const formations = [
  { id: 0, name: '', text: '', domain: '', cardAmount: 1, meanings: [''] },
  { id: 0, name: '', text: '', domain: '', cardAmount: 1, meanings: [''] },
  { id: 1, name: '', text: '', domain: '', cardAmount: 4, meanings: ['', '', '', ''] },
  { id: 2, name: '', text: '', domain: '', cardAmount: 3, meanings: ['', '', ''] },
  { id: 3, name: '', text: '', domain: '', cardAmount: 6, meanings: ['', '', '', '', '', ''] },
  { id: 4, name: '', text: '', domain: '', cardAmount: 7, meanings: ['', '', '', '', '', '', ''] },
  { id: 5, name: '', text: '', domain: '', cardAmount: 5, meanings: ['', '', '', '', ''] },
  { id: 6, name: '', text: '', domain: '', cardAmount: 5, meanings: ['', 'A', 'B', 'A', 'B']},
  { id: 7, name: '', text: '', domain: '', cardAmount: 4, meanings: ['', '', '', '']},
  { id: 8, name: '', text: '', domain: '', cardAmount: 5, meanings: ['', '', '', '', ''] },
  { id: 9, name: '', text: '', domain: '', cardAmount: 5, meanings: ['', '', '', '', ''] },
];

const questionTypes = [
  { id: 0, name: '' },
  { id: 1, name: '' },
  { id: 2, name: '' },
  { id: 3, name: '' },
  { id: 4, name: '' },
  { id: 5, name: '' }
];

const initialState = {
  mode: '',
  cardsCount: 0,
  formation: formations[0],
  questionType: questionTypes[0],
  question: '',
  result: {},
  formations,
  questionTypes,
  isPrivate: true
};

export default createReducer(initialState, {
  [STORY_MODE_SELECT.SUCCESS]: (state, payload) => {
    return {
      ...state,
      mode: payload.mode
    };
  },
  [STORY_CARDS_COUNT_SELECT]: (state, payload) => {
    return {
      ...state,
      cardsCount: payload.cardsCount
    };
  },
  [STORY_QUESTION_TYPE_SELECT.SUCCESS]: (state, payload) => {
    return {
      ...state,
      questionType: payload.questionType
    };
  },
  [STORY_QUESTION_INPUT.SUCCESS]: (state, payload) => {
    return {
      ...state,
      question: payload.question
    };
  },
  [STORY_FORMATION_SELECT.SUCCESS]: (state, payload) => {
    return {
      ...state,
      formation: payload.formation,
      cardsCount: payload.formation.cardAmount
    };
  },
  [STORY_PRIVATE.SUCCESS]: (state, payload) => {
    return {
      ...state,
      isPrivate: payload.isPrivate
    };
  },
  [STORY_GET_RESULT.SUCCESS]: (state, payload) => {
    const { result } = payload;

    return {
      ...state,
      result
    };
  },
  [NEWBIE_QUESTION_GET_RESULT.SUCCESS]: (state, payload) => {
    const { result } = payload;

    return {
      ...state,
      formation: { name: result.formation }
    };
  },
  [GET_DAILY_DIVINE.SUCCESS]: (state, payload) => {
    const { cardsInfos, card } = payload;

    return {
      ...state,
      formation: formations[0],
      result: {
        // cards_infos_array: [`##${card}#` + result.card_infos.split('#').slice(1).join('#')],
        cardsInfos,
        question: state.question,
        divine_type: ''
      }
    };
  },
  [GET_RESULT_BY_ID.SUCCESS]: (state, payload) => {
    try {
      return {
        ...state,
        result: payload
      };
    } catch(e) {
      return {
        ...state,
        result: { question: '!' }
      };
    }
  },
  [CLEAN_RESULT]: (state, payload) => {
    return {
      ...state,
      question: '',
      result: {}
    };
  }
});



